ss/bootstrap';\r\n@import '~bootswatch/dist/journal/bootswatch';\r\n@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');\r\n\r\n// html {\r\n//   zoom: 90%;\r\n// }\r\n\r\nbody {\r\n  margin: 0;\r\n}\r\n\r\na {\r\n  color: #533f03;\r\n  font-weight: bold;\r\n}\r\n\r\n* {\r\n  -moz-box-sizing: border-box;\r\n  box-sizing: border-box;\r\n  font-family: 'Montserrat', sans-serif !important;\r\n  &:after,\r\n  &::before {\r\n    -moz-box-sizing: border-box;\r\n    box-sizing: border-box;\r\n  }\r\n  // ::-webkit-scrollbar {\r\n  // display: none !important;\r\n  // }\r\n}\r\n\r\n.app-container {\r\n  box-sizing: border-box;\r\n  .view-container {\r\n    width: 100%;\r\n    height: calc(100% - 40px);\r\n    overflow-y: auto;\r\n    overflow-x: hidden;\r\n    // padding: 1rem;\r\n    .card {\r\n      // padding: 1rem;\r\n      border: none;\r\n    }\r\n    .view-routes {\r\n      height: 100%;\r\n      > div {\r\n        height: 100%;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n.fullscreen {\r\n  position: fixed;\r\n  top: 100px;\r\n  left: 0px;\r\n  width: 99% !important;\r\n  height: calc(100vh - 110px) !important;\r\n  margin: 5px;\r\n  z-index: 1000;\r\n  padding: 5px 25px 50px 25px !important;\r\n}\r\n\r\n/* ==========================================================================\r\n                        CONTENIDO FLOAT BUTTON\r\n========================================================================== */\r\n\r\nh3 {\r\n  color: #555;\r\n}\r\n\r\n#floating-button {\r\n  width: 55px;\r\n  height: 55px;\r\n  border-radius: 50%;\r\n  background: #E63946;\r\n  position: fixed;\r\n  bottom: 30px;\r\n  right: 30px;\r\n  cursor: pointer;\r\n  box-shadow: 0px 2px 5px #666;\r\n}\r\n\r\n.plus {\r\n  color: white;\r\n  position: absolute;\r\n  top: 6px;\r\n  display: block;\r\n  bottom: 0;\r\n  left: 0;\r\n  right: 0;\r\n  text-align: center;\r\n  padding: 0;\r\n  margin: 0;\r\n  line-height: 55px;\r\n  font-size: 30px;\r\n  font-family: 'Roboto';\r\n  font-weight: 300;\r\n  animation: plus-out 0.3s;\r\n  transition: all 0.3s;\r\n}\r\n\r\n#container-floating {\r\n  position: fixed;\r\n  width: 55px;\r\n  height: 55px;\r\n  bottom: 30px;\r\n  right: 30px;\r\n  z-index: 50px;\r\n  z-index: 100;\r\n}\r\n\r\n#container-floating.is-opened .plus {\r\n  animation: plus-in 0.15s linear;\r\n  animation-fill-mode: forwards;\r\n}\r\n\r\n.close {\r\n  position: absolute;\r\n  color: white !important;\r\n  top: 14px;\r\n  display: block;\r\n  bottom: 0;\r\n  left: 0;\r\n  display: block;\r\n  right: 0;\r\n  padding: 0;\r\n  opacity: 0;\r\n  margin: auto;\r\n  font-size: 28px;\r\n  line-height: 100px;\r\n  transition: all 0.3s;\r\n  animation: close-out 0.3s;\r\n}\r\n\r\n#container-floating.is-opened .close {\r\n  animation: close-in 0.2s;\r\n  animation-delay: 0.1s;\r\n  animation-fill-mode: forwards;\r\n}\r\n\r\n@keyframes close-in {\r\n  from {\r\n    opacity: 0;\r\n  }\r\n\r\n  to {\r\n    opacity: 1;\r\n  }\r\n}\r\n\r\n@keyframes close-out {\r\n  from {\r\n    opacity: 1;\r\n  }\r\n\r\n  to {\r\n    opacity: 0;\r\n  }\r\n}\r\n\r\n@keyframes plus-in {\r\n  from {\r\n    opacity: 1;\r\n  }\r\n\r\n  to {\r\n    opacity: 0;\r\n  }\r\n}\r\n\r\n@keyframes plus-out {\r\n  from {\r\n    opacity: 0;\r\n  }\r\n\r\n  to {\r\n    opacity: 1;\r\n  }\r\n}\r\n\r\n.nds {\r\n  width: 40px;\r\n  height: 40px;\r\n  border-radius: 20px;\r\n  position: fixed;\r\n  z-index: 300;\r\n  transform: scale(0);\r\n  transition: width 0.25s;\r\n}\r\n\r\n.nd1 {\r\n  // DE VERDE OSCURO A VERDESITO\r\n  // background-color: #3bb78f;\r\n  // background-image: linear-gradient(to top, #3bb78f 0%, #0bab64 100%);\r\n  // DE AZUL OSCURO A CELESTE\r\n  background-color: #7fcec5;\r\n  background-image: linear-gradient(320deg, #7fcec5 -10%, #14557b 75%);\r\n  right: 70px;\r\n  bottom: 20px;\r\n  z-index: -1;\r\n  width: 250px;\r\n  height: 280px;\r\n  animation-delay: 0.2s;\r\n  animation: bounce-out-nds 0.3s linear;\r\n  animation-fill-mode: forwards;\r\n\r\n  & > h1 {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    font-size: 25px;\r\n    height: 10%;\r\n    padding: 15px 5px;\r\n    margin-top: 20px;\r\n    font-weight: bolder;\r\n    color: white;\r\n  }\r\n\r\n  & > .estrellas {\r\n    height: 30%;\r\n    margin: -55px 0 10px 0;\r\n\r\n    & > .rating {\r\n      width: 100%;\r\n      height: 60%;\r\n      margin: 15% 6%;\r\n      // color: black;\r\n      font-size: 40px;\r\n      padding: 10px;\r\n    }\r\n  }\r\n\r\n  & > .descripcion {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    & > .input-valoracion {\r\n      width: 80%;\r\n      margin-top: 5px;\r\n    }\r\n  }\r\n\r\n  & > .button-valoracion {\r\n    width: 50%;\r\n    margin: 15px 25%;\r\n    background-color: #e63946 !important;\r\n    border-color: #e63946 !important;\r\n\r\n    &:hover {\r\n      background-color: #bd2d38 !important;\r\n      border-color: #bd2d38 !important;\r\n    }\r\n  }\r\n}\r\n\r\n@keyframes bounce-nds {\r\n  from {\r\n    opacity: 0;\r\n  }\r\n\r\n  to {\r\n    opacity: 1;\r\n    transform: scale(1);\r\n  }\r\n}\r\n\r\n@keyframes bounce-out-nds {\r\n  from {\r\n    opacity: 1;\r\n    transform: scale(1);\r\n  }\r\n\r\n  to {\r\n    opacity: 0;\r\n    transform: scale(0);\r\n  }\r\n}\r\n\r\n#container-floating.is-opened .nds {\r\n  animation: bounce-nds 0.1s linear;\r\n  animation-fill-mode: forwards;\r\n}\r\n\r\n.reminder {\r\n  position: absolute;\r\n  left: 0;\r\n  right: 0;\r\n  margin: auto;\r\n  top: 0;\r\n  bottom: 0;\r\n  line-height: 40px;\r\n}\r\n\r\n/* ==========================================================================\r\nBrowser Upgrade Prompt\r\n========================================================================== */\r\n\r\n.browserupgrade {\r\n  margin: 0.2em 0;\r\n  background: #ccc;\r\n  color: #000;\r\n  padding: 0.2em 0;\r\n}\r\n\r\n/* ==========================================================================\r\nCustom button styles\r\n========================================================================== */\r\n\r\n.icon-button > .btn {\r\n  background-color: transparent;\r\n  border-color: transparent;\r\n  padding: 0.5rem;\r\n  line-height: 1rem;\r\n  &:hover {\r\n    background-color: transparent;\r\n    border-color: transparent;\r\n  }\r\n  &:focus {\r\n    -webkit-box-shadow: none;\r\n    box-shadow: none;\r\n  }\r\n}\r\n\r\n/* ==========================================================================\r\nGeneric styles\r\n========================================================================== */\r\n\r\n/* Temporary workaround for availity-reactstrap-validation */\r\n.invalid-feedback {\r\n  display: inline;\r\n}\r\n\r\n/* other generic styles */\r\n\r\n.title {\r\n  font-size: 1.25em;\r\n  margin: 1px 10px 1px 10px;\r\n}\r\n\r\n.description {\r\n  font-size: 0.9em;\r\n  margin: 1px 10px 1px 10px;\r\n}\r\n\r\n.shadow {\r\n  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;\r\n  border-radius: 2px;\r\n}\r\n\r\n.error {\r\n  color: white;\r\n  background-color: red;\r\n}\r\n\r\n.break {\r\n  white-space: normal;\r\n  word-break: break-all;\r\n}\r\n\r\n.break-word {\r\n  white-space: normal;\r\n  word-break: keep-all;\r\n}\r\n\r\n.preserve-space {\r\n  white-space: pre-wrap;\r\n}\r\n\r\n/* padding helpers */\r\n\r\n@mixin pad($size, $side) {\r\n  @if $size== '' {\r\n    @if $side== '' {\r\n      .pad {\r\n        padding: 10px !important;\r\n      }\r\n    } @else {\r\n      .pad {\r\n        padding-#{$side}: 10px !important;\r\n      }\r\n    }\r\n  } @else {\r\n    @if $side== '' {\r\n      .pad-#{$size} {\r\n        padding: #{$size}px !important;\r\n      }\r\n    } @else {\r\n      .pad-#{$side}-#{$size} {\r\n        padding-#{$side}: #{$size}px !important;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n@include pad('', '');\r\n@include pad('2', '');\r\n@include pad('3', '');\r\n@include pad('5', '');\r\n@include pad('10', '');\r\n@include pad('20', '');\r\n@include pad('25', '');\r\n@include pad('30', '');\r\n@include pad('50', '');\r\n@include pad('75', '');\r\n@include pad('100', '');\r\n@include pad('4', 'top');\r\n@include pad('5', 'top');\r\n@include pad('10', 'top');\r\n@include pad('20', 'top');\r\n@include pad('25', 'top');\r\n@include pad('30', 'top');\r\n@include pad('50', 'top');\r\n@include pad('75', 'top');\r\n@include pad('100', 'top');\r\n@include pad('4', 'bottom');\r\n@include pad('5', 'bottom');\r\n@include pad('10', 'bottom');\r\n@include pad('20', 'bottom');\r\n@include pad('25', 'bottom');\r\n@include pad('30', 'bottom');\r\n@include pad('50', 'bottom');\r\n@include pad('75', 'bottom');\r\n@include pad('100', 'bottom');\r\n@include pad('5', 'right');\r\n@include pad('10', 'right');\r\n@include pad('20', 'right');\r\n@include pad('25', 'right');\r\n@include pad('30', 'right');\r\n@include pad('50', 'right');\r\n@include pad('75', 'right');\r\n@include pad('100', 'right');\r\n@include pad('5', 'left');\r\n@include pad('10', 'left');\r\n@include pad('20', 'left');\r\n@include pad('25', 'left');\r\n@include pad('30', 'left');\r\n@include pad('50', 'left');\r\n@include pad('75', 'left');\r\n@include pad('100', 'left');\r\n\r\n@mixin no-padding($side) {\r\n  @if $side== 'all' {\r\n    .no-padding {\r\n      padding: 0 !important;\r\n    }\r\n  } @else {\r\n    .no-padding-#{$side} {\r\n      padding-#{$side}: 0 !important;\r\n    }\r\n  }\r\n}\r\n\r\n@include no-padding('left');\r\n@include no-padding('right');\r\n@include no-padding('top');\r\n@include no-padding('bottom');\r\n@include no-padding('all');\r\n\r\n/* end of padding helpers */\r\n\r\n.no-margin {\r\n  margin: 0px;\r\n}\r\n@mixin voffset($size) {\r\n  @if $size== '' {\r\n    .voffset {\r\n      margin-top: 2px !important;\r\n    }\r\n  } @else {\r\n    .voffset-#{$size} {\r\n      margin-top: #{$size}px !important;\r\n    }\r\n  }\r\n}\r\n\r\n@include voffset('');\r\n@include voffset('5');\r\n@include voffset('10');\r\n@include voffset('15');\r\n@include voffset('30');\r\n@include voffset('40');\r\n@include voffset('60');\r\n@include voffset('80');\r\n@include voffset('100');\r\n@include voffset('150');\r\n\r\n.readonly {\r\n  background-color: #eee;\r\n  opacity: 1;\r\n}\r\n\r\n/* ==========================================================================\r\nmake sure browsers use the pointer cursor for anchors, even with no href\r\n========================================================================== */\r\n\r\na:hover {\r\n  cursor: pointer;\r\n}\r\n\r\n.hand {\r\n  cursor: pointer;\r\n}\r\n\r\nbutton.anchor-btn {\r\n  background: none;\r\n  border: none;\r\n  padding: 0;\r\n  align-items: initial;\r\n  text-align: initial;\r\n  width: 100%;\r\n}\r\n\r\na.anchor-btn:hover {\r\n  text-decoration: none;\r\n}\r\n\r\n/* ==========================================================================\r\nMetrics and Health styles\r\n========================================================================== */\r\n\r\n#threadDump .popover,\r\n#healthCheck .popover {\r\n  top: inherit;\r\n  display: block;\r\n  font-size: 10px;\r\n  max-width: 1024px;\r\n}\r\n\r\n.thread-dump-modal-lock {\r\n  max-width: 450px;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n  white-space: nowrap;\r\n}\r\n\r\n#healthCheck .popover {\r\n  margin-left: -50px;\r\n}\r\n\r\n.health-details {\r\n  min-width: 400px;\r\n}\r\n\r\n/* bootstrap 3 input-group 100% width\r\nhttp://stackoverflow.com/questions/23436430/bootstrap-3-input-group-100-width */\r\n\r\n.width-min {\r\n  width: 1% !important;\r\n}\r\n\r\n/* jhipster-needle-scss-add-main JHipster will add new css style */\r\n","/*!\n * Bootstrap v4.6.0 (https://getbootstrap.com/)\n * Copyright 2011-2021 The Bootstrap Authors\n * Copyright 2011-2021 Twitter, Inc.\n * Licensed under MIT (https://github.com/twbs/bootstrap/blob/main/LICENSE)\n */\n\n@import \"functions\";\n@import \"variables\";\n@import \"mixins\";\n@import \"root\";\n@import \"reboot\";\n@import \"ty